header{position:fixed;z-index:999;display:flex;align-items:center;justify-content:center;top:-1px;left:0;width:100%;height:var(--header);transition:translate .4s var(--ease);transform:translateY(-0.1%);font-size:14px;font-weight:500;line-height:1.5;letter-spacing:-0.01em}header nav{display:flex;align-items:center;justify-content:space-between;width:100%;height:100%;padding:0 var(--margin);margin:0 auto}header nav a{width:100%;height:100%}header nav a:hover{text-decoration:underline}header nav .-left{width:1.25rem;height:1.25rem;border-radius:50%}header nav .-center{position:absolute;display:flex;align-items:center;gap:.25em;left:50%;transform:translateX(-50%);line-height:1}header nav .-center #localTime{font-family:"JetBrains Mono",monospace}header nav .-center #timeZone{opacity:.4}@media screen and (max-width: 643px){header nav .-center{display:none}}