.row{display:grid;grid-template-columns:repeat(4, 1fr);grid-column:1/-1;gap:var(--grid-gap);padding:var(--content-row-margin) 0;border-bottom:1px solid var(--border-tint-color);overflow:hidden}.row.-fit{display:block;padding-bottom:unset;border:unset}.row.-flow{display:block;-moz-column-count:4;column-count:4;gap:var(--grid-gap);grid-auto-flow:dense;widows:3;orphans:3}.row.-flow p{margin-bottom:var(--margin)}.row.-flow p:last-child{margin-bottom:unset}@media screen and (max-width: 1023px){.row.-flow{-moz-column-count:2;column-count:2}}@media screen and (max-width: 643px){.row.-flow{-moz-column-count:auto;column-count:auto}}.row:last-child{border:none}.row .mark{position:absolute;top:var(--content-row-margin);right:0;font-weight:450}@media screen and (min-width: 644px)and (max-width: 1023px){.row .mark{right:calc(50% + var(--grid-gap)*.5)}}@media screen and (max-width: 1023px){.row{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 643px){.row{grid-template-columns:auto}}