.tagline{grid-column:1/-1;height:calc(4.125em + 1rem)}.tagline #quotes{padding:0 .125em;color:var(--surface-color);background:var(--foreground-tint-color);border-radius:.125em}@media screen and (max-width: 539px){.tagline #quotes{padding:0}}.tagline .-accent{color:var(--foreground-color) !important;background:var(--foreground-dimmed-color)}.tagline .cursor{width:0;outline:.075em solid var(--foreground-color)}.tagline .cursor.-blink{animation:blink .8s infinite}@keyframes blink{50%{opacity:0}}